import React, { useEffect, useState } from "react";
import { Drawer, Tabs } from "antd";
import "./Sidebar.scss";
import { getPostList, getTopCommentList } from "../apis/allPurposeApis";
import { DislikeOutlined, LikeOutlined } from "@ant-design/icons";
import { LoadingOutlined } from '@ant-design/icons';
import { Spin } from 'antd';
const { TabPane } = Tabs;
const antIcon = <LoadingOutlined style={{ fontSize: 24 }} spin />;
function Sidebar(props) {
  const [result, setResult] = useState([]);
  const [topComments, setTopcomments] = useState([]);
  const [pageNo, setPageNo] = useState(1);
  const [loading, setloading] = useState(false);
  useEffect(() => {
    (async () => {
      setloading(true)
      const result = await getPostList(props.website, 1);
      setResult(result?.data?.data?.results);
      setPageNo(result?.data?.data?.next_page_number)
      const topCommentsResult = await getTopCommentList(props.website);
      setTopcomments(topCommentsResult?.data?.data?.results);
      setloading(false)
    })();
  }, []);
  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  function handleScroll(e) {
    //console.log(e.target.scrollHeight - e.target.scrollTop, e.target.clientHeight)
    if (e.target.scrollHeight - e.target.scrollTop !== e.target.clientHeight) return;
    else {
      const result = async () => {
        const result = await getPostList(props.website, pageNo);
        setResult(prev => [...prev, ...result?.data?.data?.results]);
        setPageNo(result?.data?.data?.next_page_number)
        return
      }
      pageNo && result()
    }
  }
  const onClose = () => {
    try {
      props.setVisible(false);
    } catch (error) {
      console.log("Some error occured", error);
    }
  };
  const openLink = (data) => {
    try {
      window.open(data);
    } catch (error) {
      console.log("Some error occured", error);
    }
  };
  const openComment = (data) => {
    try {
      window.open(data);
    } catch (error) {
      console.log("Some error occured", error);
    }
  };
  return (

    <Drawer
      placement="right"
      closable={true}
      onClose={onClose}
      visible={props.visible}
      width={630}
    >
      <div className="Header_drawer">{props.website}</div>
      <Tabs defaultActiveKey="1">
        <TabPane tab="Latest Discussions" key="1">
          <div className="post-main" onScroll={e => handleScroll(e)}>
            {
              loading ? <Spin indicator={antIcon} /> : result?.length > 0 &&
                result.map((data, index) => (
                  <div className="post-list">
                    <div className="post-list__info">
                      <p className="post-time">
                        Recent activity {data?.updated_date}
                      </p>
                      <h2 onClick={() => openLink(data?.link)}>{data?.title}</h2>
                      <a onClick={() => openComment(data?.link)}>
                        {data?.comments_count} Comments
                    </a>
                    </div>


                  </div>
                ))}
          </div>
        </TabPane>
        <TabPane tab="Top Comments" key="2">
          <div className="post_com">
            {topComments?.length > 0 &&
              topComments.map((data, index) => (
                <div className="top_comments_post">
                  {/* <div className="avatar">
                  <Avatar
                    style={{ backgroundColor: "#dbdfe4" }}
                    shape="square"
                    size={"100%"}
                    icon={<UserOutlined />}
                  />
                </div> */}
                  <div className="post_body">
                    <div className="comment_header">
                      <div className="post_byline">
                        <div className="post_username">
                          <a href="" target="_blank">
                            {data.author_name}
                          </a>
                        </div>
                        {/* <div className="badge">
                          <Badge>MOD</Badge>
                        </div> */}
                      </div>
                      {/* <span className="bullet"></span> */}
                      <div className="post_meta">
                        <span>{data.created_date}</span>
                      </div>
                    </div>
                    <div className="comment_body">
                      <div className="post_comment">
                        <p>{data.message}</p>
                      </div>
                    </div>
                    <div className="comment_footer">
                      <div className="post_like">
                        <a href="">
                          <LikeOutlined />
                          {data.likes}
                        </a>
                      </div>
                      <div className="post_dislike">
                        <a href="">
                          <DislikeOutlined />
                          {data.dislikes}
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
          </div>
        </TabPane>
      </Tabs>
    </Drawer>);
}
export default Sidebar;
