import axios from "axios";
const baseURL = "https://demo-dexos.simform.solutions/api/";
axios.defaults.headers.common['Authorization'] = 'Api-Key 55kNqOhs.83VxNAzwkPR5MfPgIxMPn0gNGlR5wTUy'
export const getPostList = async (website, pageNo) => {
    try {
        const url = baseURL + `v1/posts/${website}/?page=${pageNo}`;
        const result = await axios.get(url);
        return result;
    } catch (error) {
        console.error(error);
    }
};
export const insertPost = async (param) => {
    try {
        const url = baseURL + "v1/post/";
        const result = await axios.post(url, param);
        return result;
    } catch (error) {
        console.error(error);
    }
};

export const insertCommets = (data) => {
    // try {
    const url = baseURL + "v1/comment/";
    return new Promise(async (resolve, reject) => {
        try {

            const result = await axios.post(url, data)
            resolve(result)
        } catch (error) {
            reject(error)
        }
    })
};

export const getCommets = (postId, currentPage) => {
    try {
        if (postId > 0) {
            if (currentPage === 1) {
                const url = baseURL + `v1/comments/${postId}/`;
                const result = axios.get(url);
                return result;
            }
            const url = baseURL + `v1/comments/${postId}/?page=${currentPage}`;
            const result = axios.get(url);
            return result;
        }
    } catch (error) {
        console.error(error);
    }
};
export const castVote = (postId, vote) => {
    try {
        const url = baseURL + `v1/comment/${postId}/`;
        const result = axios.put(url, vote);
        return result;
    } catch (error) {
        console.error(error);
    }
};
export const getTopCommentList = (websitename) => {
    try {
        const url = baseURL + `v1/top-comments/${websitename}/`;
        const result = axios.get(url);
        return result;
    } catch (error) {
        console.error(error);
    }
};
