
import React, { useEffect, useState } from "react";
import "./App.css";
import "./layout/common.scss";
import Commentcomp from "./components/Commentcomp/Commentcomp";
import { insertPost } from "../src/apis/allPurposeApis";
function App() {
  const [id, setID] = useState(0);
  const [cannotComment, setCannotComment] = useState(false);
  const [dataCount, setDataCount] = useState(0);
  var a = document.getElementById("comment");
  const params = {
    title: a.getAttribute("title"),
    link: a.getAttribute("link"),
    website: a.getAttribute("website"),
  };
  useEffect(() => {
    try {
      (async () => {
        const result = await insertPost(params);
        setID(result?.data?.data?.id);
        setCannotComment(result?.data?.data?.is_closed)
        setDataCount(result?.data?.data?.comments_count)
      }
      )()
    }
    catch (e) { console.log(e) }
  }, [])
  return (
    <div className="App">
      <Commentcomp
        ID={id}
        website={params.website}
        cannotComment={cannotComment}
        commentCount={dataCount}
      />
    </div>
  );
}

export default App;
