import React, { useState, useRef } from "react";
import { Checkbox, Form, Input, Button, Tooltip } from "antd";
import Cookies from "universal-cookie";
import Picker from "emoji-picker-react";
import useOnClickOutside from "../components/useOnClickOutside";
const cookies = new Cookies();
const { TextArea } = Input;
const CustomForm = (props) => {
  const [emojiPickerIsOpen, setemojiPickerIsOpen] = useState(false);
  const [visiblePicker, setVisiblePicker] = useState(false);
  const [email, setEmail] = useState("");
  const [name, setName] = useState("");
  const [currentMessage, setCurrentMessage] = useState("")
  const [popedArray, setPoppedArray] = useState([])
  const inputRef = useRef();
  const nameFromcookies = cookies.get("author_name");
  const emailFromCookies = cookies.get("author_email");
  const [form] = Form.useForm();
  const ref = useRef();

  useOnClickOutside(ref, () => {
    setVisiblePicker(false);
    setemojiPickerIsOpen(false);
  });
  const togglePicker = (e) => {
    try {
      e.preventDefault();
      setemojiPickerIsOpen(!emojiPickerIsOpen);
    } catch (error) {
      console.log("Some error occured", error);
    }
  };
  const onEmojiClick = (event, emojiObject) => {
    if (props.id === "MainDrawer") {
      props.messageMain
        ? props.onChange(props.messageMain + ' ' + emojiObject.emoji + ' ', props.id)
        : props.onChange(" " + emojiObject.emoji + " ", props.id);
    } else {
      props.message.message
        ? props.onChange(
          props.message.message + ' ' + emojiObject.emoji + " ",
          props.parent
        )
        : props.onChange(" " + emojiObject.emoji + " ", props.parent);
    }
  };
  const onChangeCheckBox = () => {
    props.setChecked(true);
  };
  const undoText = () => {
    try {
      const temp = currentMessage.split(' ')
      const popedvar = temp.pop()
      setPoppedArray(e => [...e, popedvar])
      props.onChange(temp.join(' '), props.id)
      setCurrentMessage(temp.join(' '))
    } catch (error) {
      console.log("Some error occured", error);
    }
  };

  const redoText = (e) => {
    try {
      const a = popedArray.length > 0 && popedArray.pop()
      const tempMessage = currentMessage.concat(' ' + a)
      a && props.onChange(tempMessage, props.id)
      a && setCurrentMessage(tempMessage)
    } catch (error) {
      console.log("Some error occured", error);
    }
  };
  const checkMesssageType = (values) => {
    try {
      if (!values.user.name || !values.user.email) {
        form.setFieldsValue({
          user: {
            name: nameFromcookies,
            email: emailFromCookies,
          },
        });

        setName(nameFromcookies);
        setEmail(emailFromCookies);
        let values = {
          user: {
            name: nameFromcookies,
            email: emailFromCookies,
          },
        };
        props.sendMessage(values, props.parent);
        return;
      } else {
        setName(values.user.name);
        setEmail(values.user.email);
        props.sendMessage(values, props.parent);
        return;
      }
    } catch (error) {
      console.log("Some error occured", error);
    }
  };
  const change = (e) => {
    props.setErrorMessage('')
    try {
      const search = (comments) => {
        comments.map((data) => {
          if (data.id === props.id) {
            setCurrentMessage(e.target.value)
            props.onChange(e.target.value, data.id);
          }
          if (data?.replies?.length > 0) {
            search(data.replies);
          }
        });
      };
      if (props.id === "MainDrawer") {
        setCurrentMessage(e.target.value)
        props.onChange(e.target.value, props.id);
      }
      search(props.comments);
      setPoppedArray('')
    } catch (error) {
      console.log("Some error occured", error);
    }
  };
  return (
    <>
      <Form
        form={form}
        validateMessages={props.validateMessages}
        onFinish={checkMesssageType}
      >
        {(props.showDrawer || props.visibleDrawer) && (
          <div className="avtar_massemmo">
            <div className="avatar_massage">
              <div className="massage_textarea">
                <TextArea
                  id="input"
                  className="mass_text"
                  placeholder="Join the discussion.."
                  rows={4}
                  value={props.messageMain || props.inputData}
                  onChange={change}
                  ref={inputRef}
                />

                <div className="commentemoji_btn"
                  ref={ref}
                  onClick={() => {
                    setVisiblePicker(true);
                  }}
                >
                  <div className="emojiundo_btn">
                    <Button

                      onClick={togglePicker}
                      className="emo_button"
                      type="primary"
                    >
                      ðŸ˜€
                    </Button>
                    {visiblePicker && emojiPickerIsOpen && (
                      <Picker
                        onEmojiClick={onEmojiClick}
                        disableSearchBar={true}
                      />
                    )}
                    <Tooltip placement="topLeft" title="Undo" color={'grey'}>
                      <Button
                        className="emo_button redo"
                        type="primary"
                        onClick={undoText}
                      ></Button>
                    </Tooltip>
                    <Tooltip placement="topLeft" title="Redo" color={'grey'}>
                      <Button
                        className="emo_button undo"
                        type="primary"
                        onClick={redoText}
                      ></Button>
                    </Tooltip>
                  </div>

                  <div className="post_comment">
                    <Button
                      className="proceed_button"
                      type="primary"
                      htmlType="submit"
                    >
                      Post
                    </Button>
                  </div>
                </div>
              </div>
            </div>
            {props.error && <div style={{ color: "#FF0000" }}>{props.error}</div>}
            <div className="loginwith_singup">
              <div className="username_emai">
                <div className="formitem_name">
                  <Form.Item name={["user", "name"]}>
                    <Input
                      placeholder="Name"
                      defaultValue={nameFromcookies ? nameFromcookies : ""}
                      value={name}
                      maxLength={30}
                    />
                  </Form.Item>
                  <Form.Item
                    rules={[
                      {
                        type: "email",
                      },
                    ]}
                    name={["user", "email"]}
                  >
                    <Input
                      type="email"
                      placeholder="Email"
                      defaultValue={emailFromCookies ? emailFromCookies : ""}
                      value={email}
                    />
                  </Form.Item>
                </div>
                <div className="privacy_policy">
                  <p className="privacy_ele">
                    *Your email address will not be published
                  </p>
                </div>
                <div className="guest_checkbox">
                  <Checkbox onChange={onChangeCheckBox}>
                    Save my name and email in this browser for the next time I
                    comment.
                  </Checkbox>
                </div>
              </div>
            </div>
          </div>
        )}
      </Form>
    </>
  );
};
export default CustomForm;
