import React, { useState, useEffect } from "react";
import { Badge } from "antd";
import {
  DislikeOutlined,
  LikeOutlined,
  MailOutlined,
  TwitterOutlined
} from "@ant-design/icons";
import "./Commentcomp.scss";
import { Button, Tooltip } from "antd";
import Sidebar from "../Sidebar";
import { insertCommets, getCommets, castVote } from "../../apis/allPurposeApis";
import Cookies from "universal-cookie";
import CustomForm from "../CustomForm";
import { Pagination } from "antd";
const cookies = new Cookies();
function Commentcomp(props) {
  const [message, setMessage] = useState({ message: "", id: 0 });
  const [messageMain, setMessageMain] = useState("");
  const [checked, setchecked] = useState(false);
  const [visible, setVisible] = useState(false);
  const [showDrawer, setShowDrawer] = useState(false);
  const [comments, setComments] = useState([]);
  const [data, setData] = useState([]);
  const [draw, setDraw] = useState({});
  const [totalPages, setTotalPages] = useState();
  const [id, setId] = useState("");
  const [like_count, setlike_count] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const [dislikelike_count, setDislikelike_count] = useState(0);
  const [error, setErrorMessage] = useState('');

  const showDrawerArticle = () => {
    setVisible(true);
  };
  let result = {};

  useEffect(() => {
    (async () => {
      try {
        result = await getCommets(props.ID, currentPage);
        setTotalPages(result?.data?.data?.total_pages);
        setData(result)

      } catch (e) {
        console.log(e)
      }
    })()

  }, [like_count, dislikelike_count, currentPage, props.ID]);



  useEffect(() => {
    function getComments(arr) {
      arr.length > 0 &&
        arr.map((i) => {
          if (i.replies?.length > 0) {
            getComments(i.replies);
          }
          i["visibleDrawer"] = false;
          i["inputData"] = "";
        });
      setComments(arr);
      return;
    }

    const resp = data?.data?.data?.results || [];
    getComments(resp);
  }, [data, comments]);


  const onChange = (e, messageId) => {
    console.log('onchange', e)
    try {
      if (messageId === "MainDrawer") {
        setId(messageId);
        setMessageMain(e);
        setCurrentPage(1)
      } else {
        setId(messageId);
        console.log('inputData', e)

        const arrayMutation = (comment, id) => {
          comment.forEach((i) => {
            if (i.replies.length > 0) {
              arrayMutation(i.replies, id);
            }
            if (i.id === id) {
              console.log('inputData', e)
              i.inputData = e;
              setComments(comment);

            }
          });
          console.log(comment, 95);
          setComments(comment);
          return
        };
        arrayMutation(comments, messageId);

        setMessage({ message: e, id: id });
      }
    } catch (error) {
      console.log("Some error occured", error);
    }
  };
  const validateMessages = {
    required: "This field is required!",
    types: {
      email: "Email is not a valid email!",
      number: "${label} is not a valid number!",
    },
    number: {
      range: "${label} must be between ${min} and ${max}",
    },
  };
  const Drawer = (e) => {
    try {
      e.preventDefault();
      setShowDrawer(true);
    } catch (error) {
      console.log("Some error occured", error);
    }
  };

  const sendMessage = async (values, parent) => {
    try {
      const email = values?.user.email
        ? values?.user.email
        : cookies.get("author_email");
      const name = values?.user.name
        ? values?.user.name
        : cookies.get("author_name");
      let data = {};

      if (id === "MainDrawer") {
        data["message"] = messageMain;
      } else {
        data["message"] = message?.message;
      }

      data["author_name"] = name;
      data["author_email"] = email;
      data["website"] = props.website;
      data["post"] = props.ID;
      data["parent"] = parent;
      if (checked) {
        cookies.set("author_name", values?.user.name, {
          maxAge: 5000, domain: window.location.hostname
        });
        cookies.set("author_email", values?.user.email, {
          maxAge: 5000, domain: window.location.hostname
        });
        //document.cookie = `domain=${window.location.host}`;
      }
      console.log(data)
      const result = await insertCommets(data, props.ID, currentPage)
      const commentsUpdated = await getCommets(props.ID, currentPage);
      setData(commentsUpdated);
      setComments((oldArray) => [...oldArray, result.data.data.results]);
      setMessageMain("");
      setMessage("");
      return result;
    }
    catch (e) {
      setErrorMessage(e.response?.data?.errors)
    }

  };

  const casetVoteFunc = async (vote, id) => {
    const data = {
      vote: vote,
    };
    try {
      const result = await castVote(id, data);
      setlike_count(result.data?.data?.likes);
      setDislikelike_count(result.data?.data?.dislikes);
    } catch (error) {
      console.log("Some error occured", error);
    }
  };

  const onchangePgination = async (current) => {
    try {
      setCurrentPage(current);
      return;
    } catch (error) {
      console.log("Some error occured", error);
    }
  };

  const openForm = (e, id) => {

    try {
      e.preventDefault();
      const arrayMutation = (comment, id) => {
        comment.forEach((i) => {
          if (i.replies.length > 0) {
            arrayMutation(i.replies, id);
          }
          if (i.id === id) {
            i.visibleDrawer = !i.visibleDrawer;
            setDraw({});
          }
        });
        setComments(comment);
      };
      arrayMutation(comments, id);
      return;
    } catch (error) {
      console.log("Some error occured", error);
    }
  };
  function createMarkup(message) {
    return { __html: message };
  }

  const commentPannel = (comment) => {
    try {

      return (
        comment.length > 0 &&
        comment.map((data, index) => {
          return (

            <li className={data.parent ? data.level > 3 ? "" : "ci_post" : "post"}>
              {/* <div className="avatar">
              <Avatar
                style={{ backgroundColor: "#dbdfe4" }}
                shape="square"
                size={"100%"}
                icon={<UserOutlined />}
              />
            </div> */}
              <div className="post_body">
                <div className="comment_header">
                  <div className="post_byline">
                    <div className="post_username">
                      <a href="" target="_blank">
                        {data?.author_name}
                      </a>
                    </div>
                    <div className="badge">
                      <Badge>MOD</Badge>
                    </div>
                  </div>
                  <span className="bullet"></span>
                  {data.parent_name && (
                    <div className="post_user">
                      <Button
                        className="postuser_arrow"
                        type="primary"
                      ></Button>
                      <span>{data?.parent_name}</span>
                    </div>
                  )}

                  <div className="post_meta">
                    <span>{data?.created_date}</span>
                  </div>
                </div>
                <div className="comment_body">
                  <div className="post_comment">
                    <p dangerouslySetInnerHTML={createMarkup(data.message)} />
                  </div>
                </div>
                <div className="comment_footer">
                  <div className="post_like">
                    <Tooltip title="Like" color={'grey'}>
                      <span
                        href=""
                        onClick={() => casetVoteFunc(1, data.id)}
                      >
                        <LikeOutlined />
                        {data.likes}
                      </span>
                    </Tooltip>
                  </div>
                  <div className="post_dislike">
                    <Tooltip title="Dislike" color={'grey'}>
                      <span
                        href=""
                        onClick={() => casetVoteFunc(-1, data.id)}
                      >
                        <DislikeOutlined />
                        {data.dislikes}
                      </span>
                    </Tooltip>
                  </div>
                  {!props.cannotComment && (
                    <div className="reply_footer">
                      <span className="bullet"></span>
                      <span
                        className="reply_text"
                        onClick={(e) => openForm(e, data.id)}
                      >
                        Reply
                      </span>
                      <span className="bullet"></span>
                    </div>
                  )}
                  <div className="share_footer">
                    <span>Share â€º</span>
                    <div className="share_hover">
                      <Button
                        className="sharehover_icon"
                        type="primary"
                        icon={<TwitterOutlined />}
                      />
                      <Button
                        className="sharehover_icon"
                        type="primary"
                        icon={<MailOutlined />}
                      />
                    </div>
                  </div>
                </div>
                {!props.cannotComment && draw && (
                  <CustomForm
                    setChecked={setchecked}
                    validateMessages={validateMessages}
                    sendMessage={sendMessage}
                    onChange={onChange}
                    message={message}
                    setMessage={setMessage}
                    visibleDrawer={data.visibleDrawer}
                    comments={comments}
                    id={data.id}
                    parent={data.id}
                    inputData={data.inputData}
                    error={error}
                    setErrorMessage={setErrorMessage}
                  />
                )}
              </div>

              {data?.replies?.length > 0 &&
                commentPannel(data.replies)}
            </li>
          );
        })
      );
    } catch (error) {
      console.log("Some error occured", error);
    }
  };

  return (
    <>
      <div className="CommentcompStyles">
        <div className="main__container">
          <div className="comment_nav">
            <ul>
              <li className="active">
                <Button type="link">{props.commentCount} Comments</Button>
              </li>
              <li>
                <Button type="link" onClick={showDrawerArticle}>
                  View all articles
                </Button>
              </li>
              <li>
                <Button type="link">Dexos Privacy Policy</Button>
              </li>
            </ul>
          </div>
          {!props.cannotComment && !showDrawer && (
            <Button
              className="jointhe"
              type="button"
              onClick={(e) => Drawer(e)}
            >
              <a className="join_text" href="">
                Join the discussion
              </a>
            </Button>
          )}

          {!props.cannotComment ? (
            <CustomForm
              setChecked={setchecked}
              setComments={setComments}
              validateMessages={validateMessages}
              sendMessage={sendMessage}
              showDrawer={showDrawer}
              onChange={onChange}
              messageMain={messageMain}
              setMessageMain={setMessageMain}
              comments={comments}
              parent={null}
              id="MainDrawer"
              error={error}
              setErrorMessage={setErrorMessage}
            />
          ) : (
              <div className="cannot_cammet">
                Comments and reactions for this thread are now closed
              </div>
            )}
          {comments.length > 0 ? <div className="all_post">
            <ul className="post_list">{commentPannel(comments, draw)}</ul>
          </div> : !props.cannotComment &&
            <div>Be the first to comment</div>
          }
        </div>

        {visible && (
          <Sidebar
            visible={visible}
            setVisible={setVisible}
            website={props.website}
          />
        )}
      </div>
      {comments.length > 0 && < Pagination
        defaultCurrent={1}
        defaultPageSize={10}
        total={totalPages * 10}
        onChange={onchangePgination}
        current={currentPage}
        showSizeChanger={false}
      />}

      <div className="FootercompStyles">
        <div className="footer_bottom">
          <div className="footer_content">
            <div className="footer_ele">
              <div className="footer_text subscribe">
                <span className="footersub_icon">
                  <MailOutlined />
                </span>
                <a href="">IPFS</a>
              </div>
              {/* <div className="footer_text disqus">
                <span className="footersub_icon">
                  <MessageFilled />
                </span>
                <a href="">Add Disqus to your site</a>
              </div>
              <div className="footer_text notselldata">
                <span className="footersub_icon">
                  <WarningFilled />
                </span>
                <a href="">Do Not Sell My Data</a>
              </div> */}
            </div>
            <div className="footer_logo">DEXOS</div>
          </div>
        </div>
      </div>
    </>

  );
}

export default Commentcomp;
